				},
				{
					url: "/static/image/2.jpg",
					text: '轮胎维修设备'
				},
				{
					url: "/static/image/3.jpg",
					text: '悬架调整状态'
				},
				{
					url: "/static/image/4.jpg",
					text: '自动系统维修状态'
				},
				{
					url: "/static/image/5.jpg",
					text: '空调设置'
				},
				{
					url: "/static/image/6.jpg",
					text: '车灯调整设置'
