	},
	methods: {
		enter(index) {
			var that = this
			uni.request({
				url: this.$store.state.url + 'System/GetPartDrawing',
				method: 'GET',
				data: {
					sparePartsType: index + 1
				},
				header: {
					'Token': this.$store.state.token
				},
				success: (res) => {
					if (JSON.parse(res.data).code == 0) {
						that.$store.state.Ninedetails = JSON.parse(res.data).data.sparePartsReclassifys
						uni.navigateTo({url: "./Navigationindetail",});
					} else {
						uni.showToast({
							title: "超时",
							icon: "none"
						})
					}
				}
			});
		}
	}
}
